.color-picker {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;

  width: fit-content;
  padding: 20px 0 32px 0;

  color: $color-default-white;
  border-radius: 20px;
  background-color: $color-night-rider;

  &__header {
    display: flex;
    justify-content: space-between;

    width: 100%;

    .btn {
      width: 25px;
      height: 25px;
    }
  }

  &__header-title {
    flex-grow: 1;

    text-align: center;
  }

  &__header-btn {
    margin-left: 10px;
  }

  &__table {
    width: fit-content;

    background-color: $color-charcoal;

    &-grid-layout {
      display: grid;

      grid-template-columns: minmax(70px, 90px) minmax(110px, 134px) repeat(4, minmax(110px, 115px));
    }

    &-row {
      min-height: 54px;

      &:not(:last-child) {
        border-bottom: 1px solid $color-night-rider;
      }

      &[draggable="true"] {
        cursor: move;
      }
    }

    &-cell {
      display: flex;
      align-items: center;
      justify-content: left;

      padding: 5px 15px;

      &:not(:last-child) {
        border-right: 1px solid $color-night-rider;
      }

      .btn {
        display: flex;

        width: 20px;
        height: 20px;
        margin: 0 auto;
      }
    }

    &-color-example {
      width: 100%;
      max-width: 41px;
      height: 100%;
      height: 41px;
    }
  }

  &__add-color-btn {
    display: flex;

    min-width: 220px;
    margin-top: 30px;
  }
}
